.container {
	margin: 15px 15px 0 15px;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(24px, 1fr));
}
button{
	margin: 0 1px;
	padding: 0px;
	background: none;
	border: 2px solid #edeef0;
	font-size: 65%;
}
.selectedPage {
	font-weight: bold;
	border: 2px solid #6fb2ff;
}